<script>
export default {
  name: 'FindTheNumber',
  components: {
  },
  data() {
    return {
      question:"20 - 6",
      answer:550,
      mathEx:["+","-","*","/"],

      userNumber:0,

      score:0,

      timeLimit: 60,
      timePassed: 0,
      timerInterval: null,

      stopGame:false,
    }
  },
  created() {
    this.getQuestion();
  },
  methods: {
    clickNumber(num){
      var vm=this;
      if(vm.stopGame)return;
      if(!vm.timerInterval) vm.startTimer();
      if(vm.userNumber > 1000) return;

      vm.userNumber *= 10
      vm.userNumber += num;

      vm.check();
    },
    check(){
      var vm=this;
      if(vm.userNumber.toLocaleString().length >= vm.answer.toLocaleString().length){
        if(vm.userNumber == vm.answer){
          vm.score += 2;
          console.log("yes")
        }else {
          vm.score -= 1;
          console.log("no")
        }

        this.userNumber = 0;
        this.getQuestion();
      }
    },
    getQuestion(){
      var num1=0,num2=0,questionPR,answerPR;
      var mathE = this.mathEx[Math.floor(Math.random() * this.mathEx.length)];

      if (mathE == "+") {
        num1 = Math.floor(Math.random() * 30);
        num2 = Math.floor(Math.random() * 30);
        
        questionPR = num1+" + "+num2
        answerPR = num1 + num2
      }else if (mathE == "-") {
        do {
          num1 = Math.floor(Math.random() * 30);
          num2 = Math.floor(Math.random() * 30);
        } while (num1 < num2);
        
        questionPR = num1+" - "+num2
        answerPR = num1 - num2
      }else if (mathE == "*") {
        num1 = Math.floor(Math.random() * (30 / 3));
        num2 = Math.floor(Math.random() * (30 / 3));
        
        questionPR = num1+" X "+num2
        answerPR = num1 * num2
      }else if (mathE == "/") {
        do {
          num1 = Math.floor(Math.random() * (30 * 2));
          num2 = Math.floor(Math.random() * (30 / 2));
        } while (num1 < num2 || num2 == 0 || num1%num2!=0);
        
        questionPR = num1+" / "+num2
        answerPR = num1 / num2
      }

      this.question = questionPR
      this.answer = answerPR
    },

    getQuestionRandomRang(){
      console.log("sdfsf")
    },


    getTimeColor(){
      var vm = this;
      if(vm.timePassed == vm.timeLimit)return 'text-red-500'
      else if(vm.timePassed >= (vm.timeLimit/2))return 'text-yellow-500'
      else return 'text-green-500'
    },

    startTimer(){
      this.timerInterval = setInterval((() => this.timePassed += 1), 1000);
    },
    stopTimer(){
      clearInterval(this.timerInterval);
    },
    TimeOver(){
      if(this.stopGame == false){
        this.stopGame = true;
        this.stopTimer();
        this.alertTimeOver();
        console.log("Time Over")
      }
    },
  
  },
  computed: {
    timer(){
      var timeLeft = this.timeLimit - this.timePassed
      if(!timeLeft)this.TimeOver();
      return timeLeft
    }
  },
  watch: {
    level(){
      this.changeSettings();
    }
  },
}
</script>

